export function runServerStream(req, next, interceptors) {
    if (interceptors) {
        next = applyInterceptors(next, interceptors);
    }
    return next(req);
}
function applyInterceptors(next, interceptors) {
    return interceptors
        .concat()
        .reverse()
        .reduce((n, i) => i(n), next);
}
